import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';

/**
 * Generated class for the SlidesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-slides',
  templateUrl: 'slides.html',
})
export class SlidesPage { 

  public filteredComments: any = [];

  constructor( public viewCtrl : ViewController,public apiProvider: ApiProvider,public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController) {
    this.filteredComments = this.navParams.get('filteredComments').slice(0);
    let index = this.navParams.get('index');
    this.rotate(index);
  }

  ionViewDidLoad() {   
    
  }

  rotate(times: number) {
    for (let index = 0; index < times; index++) {
      let item = this.filteredComments.shift(); 
      this.filteredComments.push(item);
    }
  }

public closeModals(){
    this.viewCtrl.dismiss();
}


}



